import Error from 'next/error';
import Layout from '../components/Layout';
import sanitizeHtml from 'sanitize-html';
export default class Story extends React.Component {
    static async getInitialProps({ req, res, query }) {
        const storyId = query.id;
        let story;
        try {
            const response = await fetch(`https://node-hnapi.herokuapp.com/item/${storyId}`);
            story = await response.json();

        } catch (error) {
            console.log('error')
            story = null;
        }


        return { story };
    }
    render() {
        const { story } = this.props;
        if (!story) {
            return <Error statusCode={503} />
        }
        return (
            <Layout title={story.title}>
                <div>
                    <h1><a href={story.url}>{story.title}</a></h1>
                    <div>
                        <strong> {story.comments_count || 0} comments</strong>
                        <strong>Published {story.time_ago || "unknown"} ago</strong>
                        <strong>{story.points || 0} Points</strong>
                    </div>

                    <div>
                        <p><b>{story.user}</b></p>
                        <h3>Comments</h3>

                        {story.comments.map(comment => {
                            return(
                             <div>
                                 {comment.content.replace(/(<([^>]+)>)/ig,"")}<br/>
                             </div>
                            );
                        })}
                    </div>
                </div>
            </Layout>
        )
    }
}
